.contuct-us {
  &__info {
    margin: 0 0 24px;

    &--last {
      margin-bottom: 32px;
    }
  }

  &__info-title {
    margin: 0 0 8px;
    font-size: 14px;
    line-height: 140%;

    color: $c-grey-blue;
  }

  &__info-link {
    color: inherit;
    text-decoration: none;

    transition: 0.3s;

    &:hover {
      color: $c-grey-blue;
    }
  }

  &__field {
    margin: 0 0 16px;

    &--last {
      margin-bottom: 32px;
    }
  }
}
